import React from "react"
import { HIs, HColor } from "@macashipo/mlib"
import useConfigTableCell from "../../hooks/useConfigTableCell"
import styled from "styled-components"
import { FlowGetBackgroundStyleCell } from "src/helpers/Flows/TableCell"
const WrapCell = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
`
const WrapStatus = styled.div`
  background: white;
  display: inline-block;
  border-radius: 30px;
  padding: 2px 10px;
  min-width: 20px;
  border: 2px solid #e9ecef;
  font-weight: bold;
`
export default function MTCReadonlyStatus(props) {
  const ctc = useConfigTableCell({ props })
  const { cell } = props
  if (HIs.isObject(cell)) {
    return <div></div>
  }
  let _bg =
    FlowGetBackgroundStyleCell({
      extra: ctc.extra,
      more: ctc.more,
      row: ctc.row,
    }) || "#ffffff"
  let _color = HColor.getTextWB(_bg)
  return (
    <WrapCell
      className="mct-readonly_status disabled"
      style={ctc.styleWrap(
        {},
        {
          background: "transparent",
        }
      )}
    >
      <WrapStatus style={{ background: _bg, color: _color }}>{cell}</WrapStatus>
    </WrapCell>
  )
}
